header {
  height: 120vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  margin: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 10px;
  bottom: 9rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  transform: rotate(360deg);
  font-weight: 300;
  font-size: 3rem;
  position: fixed;
  
}

@media screen and (max-width: 1024px) {
  header {
    height:140vh;
  }
}

@media screen and (max-width: 600px) {
  header {
    height: 140vh;

  }

  .header__socials,
  .scroll__down{
    display: none;
  }
}

